@keyframes showElement{
    0% {
        opacity: 0;
        top: -70px;
    }

    100% {
        opacity: 1;
        top: 0;
    }
}

.container {
    width: 100%;
    padding-top: 20px;
    position: sticky;
    top: 0;
}

.headerNav {
    width: 89%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logoWrapper {
    display: flex;
    width: 45%;
    align-items: center;
}

.logo {
    background-image: url('../../assets/img/imgonline-com-ua-Transparent-backgr-koWNgmQvujzU12.png');
    background-repeat: no-repeat;
    background-size: cover;
    width: 40px;
    height: 40px;
}

.name {
    color: #ffffff;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 24px;
    padding: 0;
    margin: 0;
    cursor: pointer;
}

.headerFixed{
    composes: container;
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: #475b6f;
    padding: 10px 0;
    animation: showElement;
    animation-duration: 1s;
}

@media screen and (min-width: 320px) and (max-width: 640px) {
    .container {
        background-color: #475b6f;
        padding: 20px 0;
    }

    .headerNav {
        display: block;
        display: flex;
        flex-direction: row-reverse;
    }

    .logoWrapper {
        width: 100%;
        flex-direction: row-reverse;
    }

    .logo {
        width: 26px;
        height: 26px;
    }
    
    .name {
        text-align: center;
        font-size: 18px;
    }
}