.container {
    width: 89%;
    margin: 0 auto;
    padding: 100px 0 0 0;
    text-align: center;
    margin-bottom: 60px;
}

.info {
    display: flex;
    width: 100%;
    justify-content: center;
    margin: 0 auto;
}

.logo {
    width: 200px;
    height: 200px;
    display: block;
    background-image: url('../../assets/img/imgonline-com-ua-Transparent-backgr-koWNgmQvujzU12.png');
    background-size: cover;
}

.desc {
    display: block;
    width: 50%;
}

.parag {
    font-size: 18px;
    margin: 0;
    margin-bottom: 8px;
    color: #4292c7;
}

@media screen and (min-width: 320px) and (max-width: 640px) {
    .info {
        display: block;
        margin: 0 auto;
        width: 100%;
    }

    .logo {
        width: 200px;
        height: 200px;
        margin: 0 auto;
        margin-bottom: 40px;
    }

    .desc {
        width: 100%;
    }
}